efined in a "),_c('a',{staticClass:"p-link",attrs:{"href":"https://dl.acm.org/authorize.cfm?key=N97906"}},[_vm._v("paper")]),_vm._v(", by Smith, Bi, and Zhai. This paper details an approach for calculating gesture clarity that I was trying to improve upon. ")])]),_c('template',{slot:"why"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" This project was done under the "),_c('a',{staticClass:"p-link",attrs:{"href":"http://www.sfu.ca/dean-gradstudies/awards/undergraduate-awards/NSERC.html"}},[_vm._v("NSERC USRA at SFU")]),_vm._v(". I was given free reign to do any project that interested me. I had dozens of ideas, and had to quickly narrow things down to a single idea that was achievable in one semester. The process of repeated refinement was an extremely freeing experience. I like taking something all the way from a very ethereal idea right through to implementation. Typically, I am only ever given the opportunity to do a very narrowly defined task, but this was quite a different experience, and I much preferred it. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" The NSERC USRA grant is done with a professor sponsoring an undergrad student. Some students participate in their sponsor's work... but my sponsor "),_c('a',{staticClass:"p-link",attrs:{"href":"https://www.sfu.ca/computing/people/faculty/binaybhattacharya.html"}},[_vm._v("Binay Bhattarachya")]),_vm._v(" gave me free reign. ")])]),_c('template',{slot:"how"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" There were several stages to implementing my idea for finding better keyboard layouts for gesture typing. First I narrowed down one solid idea, instead of 20 possibly viable ones. Then I broke my idea down into a sequence of steps that would result in the outcome I wanted. Next I proved the theoretical validity of as many steps as I could using careful mathematical proofs. Finally, I needed to prove empricially the validity of my idea with an implementation and some hard data. ")]),_c('p',{staticClass:"p-