ation of inputs creates a huge amount of work to do in evaluating each keyboard layout. My technique avoids this problem, by doing most of the input simulation using weaker, keyboard-agnostic preprocessing methods. This weaker form of input simulation is done using the comparison of similar word structures. As it turns out there are not many unique geometric structures that arise from word similarities, meaning many of ambiguities from similar gestures can be treated as identical. These weaker input simulations can then be generalized over any given keyboard layout, and only a few cheap measurements need to be done for each keyboard. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" Even better, if we use gradient descent, most of the work of estimating a keyboard layout's clarity is already done, because it is very similar to a keyboard we already evaluated. This is much less straightforward to do in older methods... but theoretically possible. ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" Once the preprocessing is done, my method takes "),_c('a',{staticClass:"p-link",attrs:{"href":"https://en.wikipedia.org/wiki/Big_O_notation"}},[_vm._v("O(alphabet_size"),_c('sup',[_vm._v("3")]),_vm._v(")")]),_vm._v(" time to evaluate the clarity of a keyboard layout, where as older methods take O(dictionary_size"),_c('sup',[_vm._v("2")]),_vm._v(") time. While theoretically this is worse "),_c('a',{staticClass:"p-link",attrs:{"href":"https://en.wikipedia.org/wiki/Computational_complexity_theory"}},[_vm._v("computational complexity")]),_vm._v(", in practice it is much faster; alphabets are "),_c('strong',[_vm._v("significantly")]),_vm._v(" smaller than dictionaries... except in languages like Chinese, which gesture typing doesn't work for anyway! ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" While it is true that my preprocessing takes O(dictionary_size"),_c('sup',[_vm._v("2")]),_vm._v("), the preprocessing only needs to be done once, not for every keyboard. ")])])],2