var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('project-template',{attrs:{"name":"BigTeamProject"}},[_c('template',{slot:"intro"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" Anyone that has ever done a group project knows it is pretty common to get people that want to coast on your hard work. That was not the case here. People worked pretty hard, but I think we bit off more than we could chew... ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" This assignment in particular was a software development project where we had an actual client. The client was from SFU's security desk, and they wanted an application built which would help them keep track of hours that employees and volunteers worked so they could replace their lack of a system known as the pile of papers on the boss' desk. None of the requirements were known beforehand, and all the tasks were complex enough that we didn't really know how to effectively break them down so we could all work on them. The class wasn't just about how to write code, but also how to work with other people so that work gets done effectively. ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" In the end it was a useful experience that I learned a great deal from. I ended up using a lot of the skills I learned out of necessity during that project to "),_c('router-link',{staticClass:"p-link",attrs:{"exact":"","to":"/projects/recursive-plug"}},[_vm._v("build this website")]),_vm._v(" you are looking at right now! ")],1),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" Unfortunately, the code is not on my GitHub. I may share it there eventually, but it isn't all that impressive. I think the experience is worth more than the resulting code. ")])]),_c('template',{slot:"what"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" I knew more than most about the tools we ended up using in this class, you wouldn't likely come across the tools we used in lower division computer science classes. Most people were pretty far out of their comfort zone in our team, I think. Which is why I ended up teaching the other people in our team how to use the tools we were working with. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" Apparently I did a pretty good job of that, because by the end of the class the professor asked if I wanted to "),_c('router-link',{staticClass:"p-link",attrs:{"exact":"","to":"/projects/teachers-assistant"}},[_vm._v("TA for another one of his classes")]),_vm._v(". ")],1),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" I also single handedly built our semi-interactive UI prototype so we could get feedback from the user on our ideas for how interaction in their application would work. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" It was a codeless prototype using "),_c('a',{staticClass:"p-link",attrs:{"href":"https://pencil.evolus.vn"}},[_vm._v("pencil")]),_vm._v(". ")])]),_c('template',{slot:"why"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" I think it is important to admit to the experiences you don't remember fondly. Often those experiences have much to teach. Actually, I am of the opinion that failure and learning are nearly synonymous; success does not teach very much. In this case, I learned a lot about how to collaborate with a team of people. Yes... that means that is where I think I failed the hardest. It is really easy to be ineffective in communication with other people, causing everyone to waste time. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" This is easily the hardest task in software development. It is an infamous problem that has sunk more projects than anything else in the world of software. Check out a "),_c('a',{staticClass:"p-link",attrs:{"href":"https://www.goodreads.com/book/show/19288230-scrum"}},[_vm._v("book")]),_vm._v(" on this topic that I like very much. ")])]),_c('template',{slot:"how"},[_c('p',{staticClass:"p-wide p-text"},[_vm._v(" I think the only thing that kept us on track at all was that we were forced to use a light form of "),_c('a',{staticClass:"p-link",attrs:{"href":"https://www.scrum.org/resources/what-is-scrum"}},[_vm._v("scrum")]),_vm._v(" during our project. We were forced to have weekly meetings, and we were also forced to keep track of things on our own gitlab issue tracker. ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" We unfortunately did not do a very good job of organizing, and I think this came down partly to us relying too much on applications like "),_c('a',{staticClass:"p-link",attrs:{"href":"https://slack.com"}},[_vm._v("slack")]),_vm._v(". There is a lot of urgency that is lost when communicating asynchronously, and the forum like discussions were a far worse way of organizing than just creating issues in our team issue tracker. When our whole team met in person, more work definitely got done, as well as when I would meet online in video calls with my teammate. We sort of grouped ourselves in the buddy system, and not all team members kept to it, but my buddy and I did, and that kept us reasonably focused and organized... at least between the two of us. ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" While apps like slack preport to improve team efficiency, I think that is not really true... In theory they sound great, but communication in a style similar to texting is pretty inefficient. Worse yet, notifications are a huge productivity killer; every time you receive a notification it pulls you out of what you are doing, and it takes time to get back to up and running. ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" Ultimately, I think we did a poor job of finding a way for everyone to work on something. At first we tried specializing, and that didn't work all that well, due to our erratic student schedules. Often, we ended up getting bottlenecked by one another's schedules. There was one thing I found worked though, and it has yet to let me down... "),_c('a',{staticClass:"p-link",attrs:{"href":"https://en.wikipedia.org/wiki/Pair_programming"}},[_vm._v("pair programming")]),_vm._v(". ")]),_c('p',{staticClass:"p-thin p-note"},[_vm._v(" I am a huge fan of pair programming, as it allows two people to work on literally the same thing. I find it often reduces stress and improves quality of work while increasing speed. ")]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" I think another mistake on our part was we picked tools that were becoming popular in the industry, as opposed to the ones we thought would be the easiest to learn. This had the effect of making me learn more, but it also made our product suffer. ")]),_c('p',{staticClass:"p-wide p-text"},[_c('strong',[_vm._v("By the end we had to learn how to use:")])]),_c('ul',{staticClass:"p-wide p-list"},[_c('li',[_vm._v("JavaScript")]),_c('li',[_vm._v("SCSS "),_c('span',{staticClass:"p-note"},[_vm._v("and later stylus")])]),_c('li',[_c('a',{staticClass:"p-link",attrs:{"href":"https://vuejs.org"}},[_vm._v("Vue.js")]),_c('ul',{staticClass:"p-list"},[_c('li',[_vm._v("Vue Router")]),_c('li',[_vm._v("VueX")]),_c('li',[_vm._v("Veutify")])])]),_c('li',[_vm._v("SFU's Auth system")]),_c('li',[_c('a',{staticClass:"p-link",attrs:{"href":"https://www.playframework.com"}},[_vm._v("Play Framework")]),_c('ul',{staticClass:"p-list"},[_c('li',[_vm._v("EBean")]),_c('li',[_vm._v("Jackson")]),_c('li',[_vm._v("Play Router")])])])]),_c('p',{staticClass:"p-wide p-text"},[_vm._v(" Moreover, we learned all of those things in four months, while trying to figure out what our client actually wanted us to do... and each of us were taking other classes. So, was it really a failure? Yes, I think so... but that is okay; I definitely learned from it and that is what school is for. ")])])],2)}
